/* Cards */
.card {
  box-shadow: 0 0 0 0 rgba(90, 113, 208, 0.11), 0 4px 16px 0 rgba(167, 175, 183, 0.33);
  -webkit-box-shadow: 0 0 0 0 rgba(90, 113, 208, 0.11), 0 4px 16px 0 rgba(167, 175, 183, 0.33);
  -moz-box-shadow: 0 0 0 0 rgba(90, 113, 208, 0.11), 0 4px 16px 0 rgba(167, 175, 183, 0.33);
  -ms-box-shadow: 0 0 0 0 rgba(90, 113, 208, 0.11), 0 4px 16px 0 rgba(167, 175, 183, 0.33);
  transition: background 0.25s ease;
  -webkit-transition: background 0.25s ease;
  -moz-transition: background 0.25s ease;
  -ms-transition: background 0.25s ease;
  border: none;
  border: none;
}
.card.card-rounded {
  border-radius: 15px;
  box-shadow: none;
  border: none;
}
.card.table-darkBGImg {
  background: #1E283D url("../../../../images/dashboard/darkBG.png") repeat-y right top;
}
.card.tale-bg {
  background: #DAE7FF;
}
.card.transparent {
  background: transparent;
}
.card .card-body {
  padding: 1.5rem 1.5rem;
}
.card .card-body + .card-body {
  padding-top: 1rem;
}
.card .card-title {
  color: #010101;
  margin-bottom: 1.2rem;
  text-transform: capitalize;
  font-size: 1.125rem;
  font-weight: 600;
}
.card .card-title.card-title-dash {
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 22px;
  color: #1F1F1F;
  margin-bottom: 2px;
}
.card .card-title.card-title-dash.card-subtitle-dash {
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 18px;
  color: #1F1F1F;
  margin: 0;
}
.card .card-subtitle {
  font-weight: 400;
  margin-top: 0.625rem;
  margin-bottom: 0.625rem;
}
.card .card-description {
  margin-bottom: 0.875rem;
  font-weight: 400;
  color: #76838f;
}
.card.card-outline-success {
  border: 1px solid theme-color("success");
}
.card.card-outline-primary {
  border: 1px solid theme-color("primary");
}
.card.card-outline-warning {
  border: 1px solid theme-color("warning");
}
.card.card-outline-danger {
  border: 1px solid theme-color("danger");
}
.card.card-faded {
  background: #b5b0b2;
  border-color: #b5b0b2;
}
.card.card-circle-progress {
  color: #ffffff;
  text-align: center;
}
.card.card-img-holder {
  position: relative;
}
.card.card-img-holder .card-img-absolute {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
}
.card.card-weather .weather-daily .weather-day {
  opacity: 0.5;
  font-weight: 900;
}
.card.card-weather .weather-daily i {
  font-size: 20px;
}
.card.card-weather .weather-daily .weather-temp {
  margin-top: 0.5rem;
  margin-bottom: 0;
  opacity: 0.5;
  font-size: 0.75rem;
}
.card.card-tale {
  background: #7DA0FA;
  color: #ffffff;
}
.card.card-tale:hover {
  background: #96b2fb;
}
.card.card-dark-blue {
  background: #4747A1;
  color: #ffffff;
}
.card.card-dark-blue:hover {
  background: #5050b2;
}
.card.card-light-blue {
  background: #7978E9;
  color: #ffffff;
}
.card.card-light-blue:hover {
  background: #8f8eed;
}
.card.card-light-danger {
  background: #F3797E;
  color: #ffffff;
}
.card.card-light-danger:hover {
  background: #f59095;
}

@media (min-width: 576px) {
  .card-columns {
    column-count: 3;
    column-gap: 1.25rem;
    orphans: 1;
    widows: 1;
  }
  .card-columns .card {
    display: inline-block;
    width: 100%;
    margin-bottom: 0.75rem;
  }
}
